<template>
	<tm-fullView>
		<tm-menubars title="左右滑动长列表" color="bg-gradient-primary-lighten" :showback="true"></tm-menubars>
		<tm-swiperList :list="list">
			<template v-slot:default="{itemdata}">
					<view style="height: 100rpx;width:100rpx">
						<tm-images :round="24" :width="100" :height="100" :src="itemdata.src"></tm-images>
					</view>
					<view>
						{{itemdata}}
					</view>
			</template>
		</tm-swiperList>
	</tm-fullView>
</template>

<script>
import tmSwiperList from '@/tm-vuetify/components/tm-swiperList/tm-swiperList.vue';
import tmMenubars from '@/tm-vuetify/components/tm-menubars/tm-menubars.vue';
import tmFullView from '@/tm-vuetify/components/tm-fullView/tm-fullView.vue';
import tmButton from '@/tm-vuetify/components/tm-button/tm-button.vue';
import tmListitem from '@/tm-vuetify/components/tm-listitem/tm-listitem.vue';
import tmImages from '@/tm-vuetify/components/tm-images/tm-images.vue';
import tmSwiperListItem from '@/tm-vuetify/components/tm-swiperListItem/tm-swiperListItem.vue';
export default {
	components: {
		tmSwiperList,
		tmMenubars,
		tmFullView,
		tmButton,
		tmSwiperListItem,
		tmListitem,tmImages
	},
	data() {
		return {
			list: []
		};
	},
	mounted() {
		let list = [];
		for (var i = 0; i < 5; i++) {
			let test = [];
			for (var j = 0; j < 1000; j++) {
				test.push(`https://picsum.photos/200?k${i}j=${j}`);
			}
			list.push(test);
		}
		this.list = list;
	},
	methods: {}
};
</script>

<style lang="scss"></style>
